import React,{ useEffect } from 'react';
import { Avatar,Box,Typography,Card, Button} from '@mui/material';
import MyMenu from '../theme/MyMenu';
import EmojiPeopleIcon from '@mui/icons-material/EmojiPeople';
import HailIcon from '@mui/icons-material/Hail';
import SchoolIcon from '@mui/icons-material/School';
import BrushIcon from '@mui/icons-material/Brush';
import AppsIcon from '@mui/icons-material/Apps';
import DownloadIcon from '@mui/icons-material/Download';
import blender from '../../referenc/A3D/blender.png'
import ZbrushCore from '../../referenc/A3D/zbrushcore.jpg'
import Substance from '../../referenc/A3D/substancePainter.jpeg'
import Quixel from '../../referenc/A3D/quixel mixer.png'
import Photoshop from '../../referenc/A3D/photoshop.jpg'
import flaga_polska from '../../referenc/flaga_polska.png'
import flaga_anglia from '../../referenc/flaga_anglia.jpg'
import cv_pol from '../../data/cv.pdf'
import cv_ang from '../../data/cv_english.pdf'
import '../../App.css'

const About = () => {

    useEffect(() => {
        window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    },[]);

  return (
    <Box className='BoxBackGround'>
        <MyMenu/>
        <Box className='BoxIconText'>
            <EmojiPeopleIcon className='Header'/>
            <Typography className='Header'>O mnie</Typography>
        </Box>
        <Card className='About'> 
            <Typography className='Description'>
                Jestem osob po studiach aktywnie poszukujc pracy w IT. 
                Moim gwnym celem jest zostanie Artyst 3D  lub Web Designerem.
                Od dawna interesuj si grami oraz projektowaniem. Chc rozwija swoje umiejtnoci oraz by czci zespou.
            </Typography>
        </Card>
        <Box className='BoxIconText'>
            <HailIcon className='Header'/>
            <Typography className='Header'>
                Dowiadczenie
            </Typography>
        </Box>
        <Card className='About'> 
            <Typography className='Description'>
                W 2022 roku podjeam praktyki w firmie Pappal Games. Moja praca polegaa na rzebieniu oraz teksturowaniu obiektw 3D do gry. 
                Importowaam je rwnie do silnika Unreal Engine 5, w ktrym przygotowywaam materia modelu.
            </Typography>
        </Card>
        <Box className='BoxIconText'>
            <SchoolIcon className='Header'/>
            <Typography className='Header'>
                Wyksztacenie
            </Typography>
        </Box>
        <Card className='About'> 
            <Typography className='Description'>
                W 2023 roku ukoczyam Politechnik witokrzysk w Kielcach. Zdobyam tytu inyniera na kierunku informatyka, specjalizacja grafik komputerowy. 
            </Typography>
        </Card>
        <Box className='BoxIconText'>
            <BrushIcon className='Header'/>
            <Typography className='Header'>
                Umiejtnoci
            </Typography>
        </Box>
        <Card className='About'>
            <Typography className='Description'>
                Posiadam umiejtnoci rzebienia, modelowania oraz teksurowania obiektw 3d.  
                Obsuga programw graficznych takich jak: ZbrushCore, Blender, Substance Painter oraz Photoshop, nie stanowi dla mnie problemu. 
                Posiadam rwnie znajomo projektowania i tworzenia stron internetowych oraz aplikacji. Moim gwnym jzykiem programowania jest JavaScript.
                Opanowam take jzyk angielski na poziomie B2. 
            </Typography>
        </Card>
        <Box className='BoxIconText'>
            <AppsIcon className='Header'/>
            <Typography className='Header'>
                Programy
            </Typography>
        </Box>
        <Card className='About'>
            <Box display={'flex'} alignItems={'center'}marginRight={2}>
                <Avatar src={blender} variant='rounded'/>
                <Typography sx={{ fontSize:20, color: 'white',marginLeft:1}}>
                    Blender
                </Typography>
            </Box>
            <Box display={'flex'} alignItems={'center'}marginRight={2}>
                <Avatar src={ZbrushCore} variant='rounded'/>
                <Typography sx={{ fontSize:20, color: 'white',marginLeft:1}}>
                    ZbrushCore
                </Typography>
            </Box>
            <Box display={'flex'} alignItems={'center'}marginRight={2}>
                <Avatar src={Substance} variant='rounded'/>
                <Typography sx={{ fontSize:20, color: 'white',marginLeft:1}}>
                    Substance Painter
                </Typography>
            </Box>
            <Box display={'flex'} alignItems={'center'}marginRight={2}>
                <Avatar src={Quixel} variant='rounded'/>
                <Typography sx={{ fontSize:20, color: 'white',marginLeft:1}}>
                   Quixel Mixer
                </Typography>
            </Box>
            <Box display={'flex'} alignItems={'center'}marginRight={2}>
                <Avatar src={Photoshop} variant='rounded'/>
                <Typography sx={{ fontSize:20, color: 'white',marginLeft:1}}>
                    Photoshop
                </Typography>
            </Box>
        </Card>
        <Box className='BoxIconText'>
            <DownloadIcon className='Header'/>
            <Typography className='Header'>
                Do pobrania
            </Typography>
        </Box>
        <Card  className='BoxColumn'>
            <Typography sx={{ fontSize:30, color: 'white',marginBottom:2}}>
               Pobierz pen wersj CV:
            </Typography>
            <Box className='BoxIconText'>
                <Avatar src={flaga_polska} variant='rounded' sx={{marginRight:1}}/>
                <Typography sx={{ fontSize:30, color: 'white',marginRight:1}}>
                    Wersja Polska:
                </Typography>
                <Button   
                    href={cv_pol} 
                    startIcon={<DownloadIcon />}
                    download="CV_Pol"
                    sx={{borderRadius:8}}
                    >Pobierz CV Polskie
                </Button>
            </Box>
            <Box className='BoxIconText'>
                <Avatar src={flaga_anglia} variant='rounded' sx={{marginRight:1}}/>
                <Typography sx={{ fontSize:30, color: 'white',marginRight:1}}>
                    Wersja Angielska:
                </Typography>
                <Button   
                    href={cv_ang} 
                    startIcon={<DownloadIcon />}
                    download="CV_Ang"
                    sx={{borderRadius:8}}
                    >Pobierz CV Angielskie
                </Button>
            </Box>
        </Card>

    </Box>
  )
};
export default About;